import { createElement, forwardRef } from "react";
import { Styled } from "./styled.types";

export const styled: Styled = (tag: any, recipes: any, variants: any) => {
  return function () {
    return forwardRef<typeof tag, any>(
      ({ as = tag, className, ...props }, ref) => {
        let recipeOptions: Record<string, string> = {};

        Object.keys(variants).forEach((key) => {
          recipeOptions[key] = props[key];
        });

        const classNames = recipes(recipeOptions);

        return createElement(as, {
          ref,
          ...props,
          className: `${className} ${classNames}`,
        });
      }
    );
  };
};
